import React, { useState, useEffect } from 'react'
// import TabsInfo from './TabsInfo'
import Loading from './Loading'
import { FaAngleDoubleRight } from 'react-icons/fa'
const url = 'https://course-api.com/react-tabs-project'
const Tabs = () => {
  const [isLoading, setIsLoading] = useState(true)
  const [num, setNum] = useState(1)
  const [details, setDetails] = useState([])
  const [all, setAll] = useState(false)
  const [readMore, setReadMore] = useState(true)

  const fetchInfo = async () => {
    try {
      const response = await fetch(url)
      const data = await response.json()
      setDetails(data)
      setIsLoading(false)
    } catch (error) {
      setIsLoading(false)
      console.log(error)
    }
  }
  useEffect(() => {
    fetchInfo()
  }, [])

  const handleClick = (id) => {
    setNum(id)
  }
  const handleReadMore = (index) => {
    if (all) {
      setAll(false)
      setReadMore(true)
    } else {
      setAll(true)
      setReadMore(false)
    }
  }
  if (isLoading) {
    return <Loading />
  }
  const { company, dates, duties, title } = details[num]
  // console.log(details[index])
  return (
    <>
      <section className='center'>
        <div className='title text-center'>
          <h1>Experience</h1>
          <div className='line '></div>
        </div>
        {/* <TabsInfo details={details[index]} /> */}
        <section className='row wrapper'>
          <aside className='side-bar col-sm-3'>
            {details.map((org, index) => {
              return (
                <>
                  <button
                    key={index}
                    className={`item ${num === index && 'btn-active'}`}
                    onClick={() => handleClick(index)}
                  >
                    {org.company}
                  </button>
                </>
              )
            })}
            {/* <p className='item'>{company}</p> */}
          </aside>
          <article className='col-sm-9 main'>
            <h2 className='title-2'>{title}</h2>
            <h5 className='name'>{company}</h5>
            <p className='date text-muted'>{dates}</p>
            {duties.map((duty, index) => {
              return (
                <div className='group'>
                  <button className='btn icon'>
                    <FaAngleDoubleRight />
                  </button>
                  <p className='info'>
                    {all ? duty : `${duty.substring(0, 100)}... `}

                    <button
                      className='click'
                      onClick={() => handleReadMore(index)}
                    >
                      {readMore ? 'Read More' : 'Read Less'}
                    </button>
                  </p>
                </div>
              )
            })}

            {/* <div className='button'>
              <button className='btn text-uppercase'>more info</button>
            </div> */}
          </article>
        </section>
      </section>
    </>
  )
}

export default Tabs
